.experiencia {
    max-width: 1200px;
    padding: 80px 20px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 2fr;
    gap: 100px
}

.experiencia .subtitulo {
    color: #ccc
}

.experiencia-texto {
    font-size: 1.5rem;
    line-height: 1.33;
    max-width: 40ch;
    margin-bottom: 60px;
    color: #3d3d3d
}

.experiencia-texto strong {
    color: #41819d
}

.empresa {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px 20px;
    padding: 20px;
    background: #e0e0e0;
    margin-bottom: 20px;
    border-radius: 4px;
    position: relative
}

.empresa::before {
    content: "";
    display: block;
    width: 4px;
    height: 20px;
    background: linear-gradient(180deg, #118fca 0, #97d1ec 100%);
    position: absolute;
    top: 20px;
    left: -4px
}

.empresa-ano {
    position: absolute;
    top: 22px;
    left: -100px;
    color: #141414;
    font-size: .875rem
}

.empresa-titulo {
    font-size: 1.125rem;
    line-height: 1.1;
    font-weight: 700
}

.empresa-texto {
    font-size: .875rem;
    line-height: 1.4;
    color: #3d3d3d
}

.empresa-habilidades {
    display: flex;
    flex-wrap: wrap;
    gap: 10px;
    align-content: start
}

.empresa-habilidades li {
    font-size: .875rem;
    line-height: 1.4;
    border-radius: 4px;
    background: #fff;
    padding: 5px 10px
}

@media (max-width:800px) {
    .experiencia {
        grid-template-columns: 1fr;
        gap: 40px
    }

    .experiencia .subtitulo {
        color: #141414
    }

    .experiencia-texto {
        font-size: 1.25rem
    }

    .empresa-ano {
        position: initial;
        order: 1;
        width: initial;
        text-align: left
    }
}

@media (max-width:400px) {
    .empresa {
        grid-template-columns: 1fr
    }
}