body {
    font-family: Dosis, sans-serif;
    margin: 0;
    color: #141414;
    box-sizing: border-box;
}

h1,
h2,
h3,
p {
    margin: 0
}

ul {
    margin: 0;
    padding: 0;
    list-style: none
}

img {
    max-width: 100%;
    display: block;
}

.subtitulo {
    font-size: 8rem;
    line-height: 1;
    text-transform: uppercase;
    word-break: break-all;
    max-width: 4ch
}

@media (max-width:800px) {
    .subtitulo {
        font-size: 3rem;
        text-transform: capitalize;
        max-width: initial !important
    }
}