.introducao1 {
    background: #f5f5f5
}

.introducao {
    max-width: 1200px;
    padding: 60px 20px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 2fr;
    gap: 100px;
    align-items: center
}

.introducao h1 {
    font-size: 4.5rem;
    line-height: 1.125;
    margin-bottom: 30px;
    position: relative
}

.introducao h1::before {
    content: "";
    display: block;
    width: 30px;
    height: 150px;
    background: url("../img/DECORAÇÃO.svg") no-repeat center;
    position: absolute;
    top: -50px;
    left: -30px
}

.introducao p {
    font-size: 1.5rem;
    color: #3d3d3d
}

@media (max-width:1000px) {
    .introducao h1 {
        font-size: 3rem
    }
}

@media (max-width:800px) {
    .introducao {
        grid-template-columns: 1fr 1fr;
        gap: 40px
    }

    .introducao h1 {
        font-size: 2rem
    }

    .introducao h1::before {
        width: 30px;
        height: 10px;
        left: 0;
        top: -10px
    }

    .introducao p {
        font-size: 1rem
    }
}